/* eslint-disable */
var getRandomValues = require('polyfill-crypto.getrandomvalues');

function OnepayUtil() {

  this.createUuidv4 = function () {


    var altUuid = ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, function (c) {
      var arr = new Uint8Array(1);
      return (c ^ getRandomValues(arr)[0] & 15 >> c / 4) .toString(16);
    });
    return altUuid;
/*    return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, function (c) {
      return (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16);
    });*/
  };
}

module.exports = OnepayUtil;
